import React, { Component } from 'react';
import SectionTitle from '../common/SectionTitle.js'
import Separation from '../common/Separation.js'
import AreaElement from './AreaElement.js'
import './AreasOfInterest.css'

class AreasOfInterest extends Component{
    componentDidMount(){
        const elements = document.getElementsByClassName('donut-segment')
        
        const animate = element => (
            element.classList.add('animate-donut-segment')
        );

        const deanimate = element => (
            element.classList.remove('animate-donut-segment')
        );
          
        const intersectionObserver = new IntersectionObserver((entries, observer) => {
            entries.forEach((entry) => {
              
              // when element's is in viewport,
              // animate it!
              if (entry.intersectionRatio > 0) {
                animate(entry.target);
                // remove observer after animation
              } else {
                deanimate(entry.target);
              }
            });
          });

          for(let i = 0; i < elements.length; i++){
            intersectionObserver.observe(elements[i])
          }
    }

    render(){
        return(
            <section id="areas-interest-container">
                <SectionTitle title="Areas Of Interest"/>
                <Separation />
                <div className="grid-container">
                    <AreaElement name="Network Security" interest="95"/>
                    <AreaElement name="Software Security" interest="100"/>
                    <AreaElement name="Web Development" interest="90"/>
                    <AreaElement name="Distributed Systems" interest="85"/>
                    <AreaElement name="Compilers" interest="85"/>
                    <AreaElement name="Operating Systems" interest="80"/>
                </div>
            </section>
        );
    }

}

export default AreasOfInterest;