import React, { Component } from 'react';
import ReactDOM from 'react-dom'

import './NavbarLink.css'

const ELEMENT_IDS = ['profile-container', 'personal-container', 'areas-interest-container', 'portfolio-container', 'contact-container']
const TITLE_Y_OFFSET = 100

class NavbarLink extends Component{
    constructor(props){
        super(props)

        this.handleClick = this.handleClick.bind(this)
        this.elementID = this.props.id
        this.linkElement = document.getElementById(this.elementID)
    }

    componentDidMount(){
        ReactDOM.findDOMNode(this).addEventListener('click', this.handleClick)
    }

    componentWillUnmount(){
        ReactDOM.findDOMNode(this).removeEventListener('click', this.handleClick)
    }

    /**
	 * Smoothly scrolls down a page until a certain height
	 * given an initial height.
	 * @param {*} elemHeight the ending height
	 * @param {*} actualHeight the staring height
	 */
	smoothDownScroll(elemHeight, actualHeight){
	    let currentHeight = actualHeight, timer;
	    elemHeight -= 20;
	
	    timer = setInterval(function(){
	        if(currentHeight >= elemHeight){
	            clearInterval(timer);
	        } else {
	            currentHeight += 4;
	            window.scrollTo(0, currentHeight);
	        }
	    }, 2);
	}

    handleClick = (event) => {
        event.preventDefault()
    
        let btnIndex = parseInt(this.props.index)
        let elementToScroll = document.getElementById(ELEMENT_IDS[btnIndex])
        let scrollValue = elementToScroll.offsetTop-TITLE_Y_OFFSET

        //this.smoothDownScroll(scrollValue, this.clientHeight)

        window.scrollTo({
            top: scrollValue,
            behavior: 'smooth'
        })
        
        // prevents the menu to be opened when the left side link (home link)
        // is clicked. 
        if(btnIndex === 0){
            return
        }

        this.props.showMenuMethod()
    } 

    render(){
        return(
            <li><button id={this.elementID} className="link">{this.props.name}</button></li>
        );
    }
}

export default NavbarLink;