import React, { Component } from 'react';
import './Portfolio.css'
import SectionTtile from '../common/SectionTitle';
import Separation from '../common/Separation';
import ProjectContainer from './ProjectContainer';

const ACTIVE_PROJECTS = 0;
const INACTIVE_PROJECTS = 1;

class Portfolio extends Component{
    constructor(){
        super()
        
        this.currPanel = ACTIVE_PROJECTS;
    }

    // Active Panel Utility Functions

    hideActivePanel(){
        let activePanel = document.getElementById('activePanel')
        activePanel.style.display = 'none'
    }

    showActivePanel(){
        let activePanel = document.getElementById('activePanel')
        activePanel.style.display = 'block'
    }

    updateActivePanel(){
        if(this.currPanel === ACTIVE_PROJECTS) return;

        this.removeActivePanelBottomBorder(document.querySelector('#inactiveTab p'))
        this.paintActivePanelBottomBorder(document.querySelector('#activeTab p'))
        this.hideInactivePanel()
        this.showActivePanel()
        this.currPanel = ACTIVE_PROJECTS
    }

    // Inactive Panel Utility Functions

    hideInactivePanel(){
        let inactivePanel = document.getElementById('inactivePanel')
        inactivePanel.style.display = 'none'
    }

    showInactivePanel(){
        let inactivePanel = document.getElementById('inactivePanel')
        inactivePanel.style.display = 'block'
    }

    updateInactivePanel(){
        if(this.currPanel === INACTIVE_PROJECTS) return;

        this.removeActivePanelBottomBorder(document.querySelector('#activeTab p'))
        this.paintActivePanelBottomBorder(document.querySelector('#inactiveTab p'))
        this.hideActivePanel()
        this.showInactivePanel()
        this.currPanel = INACTIVE_PROJECTS
    }

    // Common 

    removeActivePanelBottomBorder(elem){
        elem.style.border = 'none';
    }

    paintActivePanelBottomBorder(elem){
        elem.style.borderBottom = 'solid 2px #FE5F55';
    }

    render(){
        return(
            <section id="portfolio-container">
                <SectionTtile title="Portfolio" />
                <Separation />
                <div className="panelChoosingTab">
                    <div id="activeTab" onClick={() => {this.updateActivePanel()}}>
                        <p><strong>Active</strong></p>
                    </div>
                    <div id="inactiveTab" onClick={() => {this.updateInactivePanel()}}>
                        <p><strong>Inactive</strong></p>
                    </div>
                </div>
                <section id="activePanel" className="projectsPanel">
                    <ProjectContainer 
                        title="MaTheX2Java - Online LaTeX to Java Converter" 
                        date="24th March, 2019" 
                        description="MatheX2Java is a project which started to be developed in late March 2019. It is a web application which converts LaTeX code - from the amsmath package - to Java code, in real-time. It converts mathematic formulas into java methods which can then be invoked and executed. 
                        It's first design was very simple, which only considered raw amsmath code. In a second, deeper approach to the project specification, the application design was upgraded, considering the peculiar properties of the Java programming language. Hence, it was implemented an auxiliary grammar which allows annotations, in the form of LaTex comments to specify important details of how the Java code must be generated."
                        languages="ANTLR4 + NodeJS + Express + Jest + PUG" 
                        status="active"
                        imgPath="/images/projects/inesc-tec_logo.png"/>
                </section>
                <section id="inactivePanel" className="projectsPanel">
                    <ProjectContainer 
                        title="INESC-TEC Network Security Research Assistant" 
                        date="15th June, 2019" 
                        description="Consists on defining the behaviour of the communications between IoT devices, which use the publish-subscribe communication pattern, in ROS (Robot Operating Systems) environments, encompassing normal and intrusive traffic. This characterization of the behaviour between IoT systems/devices is a crucial feature for today's communication security."
                        languages="ROS + Python" 
                        status="active"
                        imgPath="/images/projects/inesc-tec_logo.png"/>
                </section>
            </section>

            
        );
    }
}

export default Portfolio;