import React, { Component } from 'react';
import ReactDOM from 'react-dom'
import './Contact.css'
import Separation from '../common/Separation';
import SectionTtile from '../common/SectionTitle';

class Contact extends Component{
    constructor(){
        super() 
        this.state = {
            mailVisible: false
        }
    }

    componentDidMount(){ 
        let emailBtn = document.getElementById('email-container')
        ReactDOM.findDOMNode(emailBtn).addEventListener('click', this.toggleEmail)
    }

    componentWillUnmount(){
        let emailBtn = document.getElementById('email-container')
        ReactDOM.findDOMNode(emailBtn).removeEventListener('click', this.toggleEmail)
    }

    toggleEmail = (event) => {
        let emailBtn = document.getElementById('email-container')
        
        if(this.state.mailVisible){
            emailBtn.innerHTML = 'SHOW ME THE EMAIL'
            this.setState({ mailVisible: false })
        } else {
            emailBtn.innerHTML = 'franciscomaria97@hotmail.com'
            this.setState({ mailVisible: true })
        }
    }

    render(){
        return(
            <section id="contact-container">
                <SectionTtile title="Contact" />
                <Separation />
                <p>For any questions, proposals or if you just want to get in touch with me, please send an email to my personal email address.
                    I'll get back to you as soon as possible.
                </p>
                <button id="email-container">SHOW ME THE EMAIL</button>
            </section>
        );
    }
}

export default Contact;