import React, { Component } from 'react';
import NavbarLink from './NavbarLink'

import './Navbar.css'

class Navbar extends Component{
    constructor(props){
        super(props)

        this.state = {
            refLinksToggled: false
        }
    }

    /**
     * Shows / hides the reference-links container 
     * according to the actual state, in the mobile version. 
     */
    toggleRefLinks(){
        if(window.innerWidth > 600) return   // not in mobile mode

        let refLinks = document.getElementById('ref-links')
        let menuIcon = document.getElementById('menuBtn')

        if(this.state.refLinksToggled){  //hide
            menuIcon.style.opacity = '1'
            refLinks.style.height = '0px'
        } else {   //show
            menuIcon.style.opacity = '0.6'
            refLinks.style.height = '170px'
        }
        
        this.setState({ 
            refLinksToggled: !this.state.refLinksToggled
        })
    }

    render(){
        return(
            <div id='top-navbar'>
                <div id="home-container">
                    <NavbarLink id="Home" index="0" name="FM @ Software Developer" showMenuMethod={this.toggleRefLinks.bind(this)}/>
                    <div id="menuBtn" className="menu-icon" onClick={() => {this.toggleRefLinks()}}>
                        <i className="fas fa-bars"></i>
                    </div>
                </div>
                <ul id="ref-links">
                    <NavbarLink id="personal-info-link" index="1" name="Personal Info" showMenuMethod={this.toggleRefLinks.bind(this)}/>
                    <NavbarLink id="interests-link" index="2" name="Areas of Interest" showMenuMethod={this.toggleRefLinks.bind(this)}/>
                    <NavbarLink id="portfolio-link" index="3" name="Portfolio" showMenuMethod={this.toggleRefLinks.bind(this)}/>
                    <NavbarLink id="contact-link" index="4" name="Contact" showMenuMethod={this.toggleRefLinks.bind(this)}/>
                </ul>
            </div>
        );
    }
}

export default Navbar;